
@media only screen and (min-width: 599px) {

    .shop-page {
        margin: 150px 30px;
    }
    

}

@media only screen and (max-width: 600px) {


    .shop-page {
        margin: 150px 10px;
    }
    
}

.shop-items {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.shop-item {
    /* display: flex; */
    flex-wrap: wrap;
    align-items: center;
    border-top: 1px solid black;
    padding-bottom: 20px;
    padding-top: 6px;
}

.item-info {
    flex: 3;
}

.item-image {
    flex: 2;
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
}

.item-image img {
    max-width: 100%;
    max-height: 500px;
    height: auto;
    object-fit: cover;
}

.item-price {
    flex: 1;
}

.purchase-link {
    flex: 1;
    margin-bottom: 22px;
}

.purchase-link a {
    color: var(--grey);
    text-decoration: none;
}

.purchase-link a:hover {
    /* text-decoration: underline; */
    color: black;
}

.shop-item h3, .shop-item p, .shop-item a {
    font-size: var(--font-size-body);
    font-weight: normal;
    line-height: 1.2;
}